<template>
  <div class="footer-container">
    <Header>
      <template #leftTitle>
        {{t('index.Oasis兌換TOP')}}
      </template>
    </Header>
    <div class="swap">
      <div class="swap-card region-bg">
        <div class="input-title">Oasis{{t('index.兌換')}}</div>
        <div class="input-wrap cell-bg">
          <input v-model="oasis.amount" type="number" :placeholder="t('index.請輸入兌換金額')">
          <div class="label">Oasis</div>
        </div>
        <img class="icon-swap" src="@/assets/images/swap.png" alt="">
        <div class="input-title" style="padding-top:23px;">{{t('index.兌換數量')}}</div>
        <div class="input-wrap cell-bg">
          <input v-model="oasis.amount" type="number" :placeholder="t('index.請輸入兌換金額')">
          <div class="label">TOP</div>
        </div>
        <div class="des">{{t('index.兌換比例：')}}1Oasis=1TOP</div>
        <div class="btn-wrap">
          <van-button v-if="!Number(oasis.allowance)" class="btn" type="primary" :loading="oasis.loading" :disabled="oasis.loading" :loading-text="t('index.參與')+'...'" @click="approveOasisHandle">{{t('index.參與')}}</van-button>
          <van-button v-else class="btn" type="primary" :loading="oasis.loadingSwap" :disabled="oasis.loadingSwap" :loading-text="t('index.兌換')+'...'" @click="swapHandle">{{t('index.兌換')}}</van-button>
        </div>
        <div class="tip">{{t('index.每次兌換即按照兌換比例進行兌換')}}</div>
      </div>
      <div class="record">
        <div class="table-title">{{t('index.兌換記錄')}}</div>
        <div class="table region-bg">
          <div class="thead">
            <div class="td">{{t('index.支付')}}<span>(Oasis)</span></div>
            <div class="td">{{t('index.獲得')}}<span>(TOP)</span></div>
            <div class="td">{{t('index.時間')}}</div>
          </div>
          <div class="tbody">
            <div class="tr" v-for="(item,index) in 5" :key="index">
              <div class="td">100</div>
              <div class="td">10000000</div>
              <div class="td">2022.06.23 10:20:30</div>
            </div>
          </div>
        </div>
      </div>
      <Footer></Footer>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import { reactive, toRefs, computed, watch, getCurrentInstance } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { config } from "@/utils/config";
import oasisAbi from "@/utils/oasisAbi";
import swaptopAbi from "@/utils/swaptopAbi";
import BigNumber from "bignumber.js";
import { MaxUint256 } from "@ethersproject/constants";
import {
  Toast
} from "vant";
export default {
  components: {
    Header,
    Footer
  },
  setup () {
    const { proxy } = getCurrentInstance();
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      oasisContract: null,
      SWAPTOPContract: null,
      oasis: {
        loading: false,
        allowance: 0,
        amount: '',
        loadingSwap: false
      },
      initContract: () => {
        data.oasisContract = new data.web3.eth.Contract(
          oasisAbi,
          config.oasisAddress
        );
        console.log('SWAPTOP:', config.SWAPTOP)
        data.SWAPTOPContract = new data.web3.eth.Contract(
          swaptopAbi,
          config.SWAPTOP
        );
        data.initData()
      },
      initData: () => {
        data.getAllowanceOasis()
      },
      approveOasisHandle: () => {
        data.oasis.loading = true
        data.oasisContract.methods
          .approve(config.SWAPTOP, MaxUint256)
          .send({ from: data.address })
          .then(async () => {
            data.oasis.loading = false
            Toast(t("index.批准成功"));
            await data.getAllowanceOasis();
          })
          .catch((error) => {
            console.log(error)
            data.oasis.loading = false
            Toast(t("index.批准失敗"));
          });
      },
      getAllowanceOasis: async () => {
        let allowance = await data.oasisContract.methods
          .allowance(data.address, config.SWAPTOP)
          .call({ from: data.address });
        console.log(allowance, 'getAllowanceOasis')
        data.oasis.allowance = allowance;
        return allowance;
      },
      swapHandle: async () => {
        try {
          if (!data.oasis.amount) {
            return Toast(t('index.請輸入兌換的數量'))
          }
          let amount = data.oasis.amount
          let balance = await data.oasisContract.methods
            .balanceOf(data.address)
            .call({ from: data.address })
          let decimals = await data.oasisContract.methods
            .decimals()
            .call({ from: data.address })
          amount = new BigNumber(amount)
            .multipliedBy(10 ** decimals)
            .toFixed();
          console.log('amount:', amount, 'balance:', balance, 'decimals:', decimals)
          if (new BigNumber(amount).comparedTo(balance) === 1) {
            // 大于为1，小于为-1，等于为0
            return Toast(t("index.餘額不足"));
          }
          data.oasis.loadingSwap = true
          let _gas = await data.SWAPTOPContract.methods
            .swap(amount)
            .estimateGas({
              from: data.address
            });
          console.log(_gas, '_gas')
          await data.SWAPTOPContract.methods
            .swap(amount)
            .send({
              from: data.address
            });
          Toast(t("index.Success"));
          data.oasis.amount = ''
          data.oasis.loadingSwap = false
        } catch (error) {
          data.oasis.loadingSwap = false
          proxy.$errorformat(error)
        }
      },
      getBalance: async () => {
        try {
          let balance = await data.oasisContract.methods
            .balanceOf(data.address)
            .call({ from: data.address })
          console.log(balance, 'balance')
        } catch (error) {
          proxy.$errorformat(error)
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
</style>
<style lang="less" scoped>
.swap {
  padding-bottom: 16px;
  .swap-card {
    text-align: left;
    padding: 17px 15px;
    position: relative;
    margin-bottom: 15px;
    .input-title {
      font-size: 15px;
      font-weight: 500;
      color: #00edf8;
      margin-bottom: 12px;
    }
    .cell-bg {
      padding: 0 13px;
      min-height: initial;
      display: flex;
      align-items: center;
    }
    .label {
      flex-shrink: 0;
      font-size: 15px;
      font-weight: 600;
      color: #00edf8;
    }
    input {
      background: transparent;
      font-size: 13px;
      font-weight: 500;
      color: #b1beea;
      text-align: left;
      height: 46px;
      width: 100%;
      &::placeholder {
        color: #b1beea;
      }
    }
    .icon-swap {
      display: block;
      position: absolute;
      left: 50%;
      top: 110px;
      transform: translateX(-50%);
      width: 27px;
      height: auto;
    }
    .btn-wrap {
      margin: 12px 0 10px;
      .van-button {
        width: initial;
        min-width: 113px;
        padding: 0 20px;
      }
    }
    .des {
      padding-top: 9px;
      font-size: 12px;
      font-family: PingFang SC;
      font-weight: 500;
      color: #b1beea;
    }
    .tip {
      font-size: 12px;
      font-weight: 500;
      color: #b1beea;
    }
  }
  .record {
    .table-title {
      width: 220px;
      height: 31px;
      background: url(~@/assets/images/table-title.webp) no-repeat;
      background-size: 100% 100%;
      text-align: center;
      margin: 0 auto 17px;
      position: relative;
      line-height: 31px;
      font-size: 16px;
      font-weight: 600;
      color: #00edf8;
      text-shadow: 0px 1px 0px rgba(73, 0, 146, 0.35);
    }
    .table {
      padding: 19px 23px 19px;
      .thead {
        display: flex;
        align-items: center;
        padding: 10px 0;
        .td {
          font-size: 14px;
          font-weight: 500;
          color: #5bffff;
          text-align: left;
          span {
            font-size: 12px;
          }
          &:nth-child(1) {
            width: 27.66%;
          }
          &:nth-child(2) {
            width: 30%;
          }
          &:nth-child(3) {
            text-align: center;
            width: 40%;
          }
        }
      }
      .tbody {
        .tr {
          display: flex;
          align-items: center;
          .td {
            text-align: left;
            font-size: 12px;
            &:not(:last-child) {
              margin-bottom: 10px;
            }
            color: #fff;
            &:nth-child(1) {
              width: 22.66%;
            }
            &:nth-child(2) {
              width: 38.86%;
            }
            &:nth-child(3) {
              width: 40%;
            }
          }
        }
      }
    }
  }
}
</style>
